import React from "react";
import { useQuery, gql } from '@apollo/client';

export const GET_CONTENT = gql`
  query {
    navBar {
      id
      logo_text
      navLinks {
        id
      }
    }
    header {
      id
      heading
      description_1
      button_text
      sub_heading
      description_2
    }
    aboutSection {
      id
      aboutHeading
      aboutBody
    }
    servicesSection {
      id
      title
      btnText1
      btnText2
      btnText3
      footerContent
    }
    contactSection {
      id
      smallText
    }
  }
`;