import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import { camsol_logo_white, camsol_logo_shaded } from "../../resources/images";
import { AiOutlineClose, GiHamburgerMenu } from "../../components";
import styles from "./Navbar.module.css";

const TopNav = (props) => {
  const [ToggleNav, setToggleNav] = useState(false);
  return (
    <nav className={`${styles.topNavWrapper}`}>
      <a href="/" className="inline-flex xl:ml-20 ml:5 my-5 mr-auto flex-col  w-[55px] lg:w-[125px]">
        <img className="h-auto w-auto" alt="camsol logo" src={camsol_logo_shaded} />
      </a>
      <div className={`${styles.topNavLinksWrapper} text-white`}>
        <NavLink to="/" className={`${styles.topNavLinks} font-bold font-serif hover:text-gray-300`}>
          Incubation
        </NavLink>
        <NavLink to="/" className={`${styles.topNavLinks} font-bold font-serif hover:text-gray-300`}>
          Transformation
        </NavLink>
        <NavLink to="/" className={`${styles.topNavLinks} font-bold font-serif hover:text-gray-300`}>
          Performance
        </NavLink>
        <NavLink to="/" className={`${styles.topNavLinks} font-bold font-serif hover:text-gray-300`}>
          About
        </NavLink>
      </div>

      {/* Mobile Menu Toggle button */}
      <div className="text-almost-white xl:hidden">
        <button
          className="p-2 rounded-full"
          onClick={() => setToggleNav((p) => !p)}
        >
          {ToggleNav ? <AiOutlineClose /> : <GiHamburgerMenu />}
        </button>
      </div>

      {/* Mobile Menu */}
    </nav>
  );
};

export default TopNav;
