import React from "react";
import { motion, useAnimation } from "framer-motion";
import { useEffect } from "react";
import { useInView } from "react-intersection-observer";
import { AboutCard } from "../../components";
import "./About.module.css";
import { startup1, startup2, about_card, about_extra_1, about_extra_2 } from "../../resources/images";
import { useQuery } from "@apollo/client";
import { GET_CONTENT } from "../../data";


const about_card_variants = {
  visible: { opacity: 1, y: 0, transition: { duration: .4 }},
  hidden: { opacity: 0, y: 400, transition: { duration: .4 }},
}
const heading_variants = {
  visible: { opacity: 1, y: 0, transition: { duration: .6 }},
  hidden: { opacity: 0, y: -100, transition: { duration: .6 }},
}
const left_text_variants = {
  visible: { opacity: 1, x: 0, transition: { duration: .6 }},
  hidden: { opacity: 0, x: -50, transition: { duration: .6 }},
}

const right_text_variants = {
  visible: { opacity: 1, x: 0, transition: { duration: .6 }},
  hidden: { opacity: 0, x: 50, transition: { duration: .6 }},
}

const extra_variants = {
  visible: { opacity: 1, x: 0, transition: { duration: .6 }},
  hidden: { opacity: 0, x: -200, transition: { duration: .6 }},
}

function About() {
  const control = useAnimation()
  const control2 = useAnimation()
  const control3 = useAnimation()
  const control4 = useAnimation()
  const [ref, inView] = useInView()
  const [ref2, inView2] = useInView()
  const [ref3, inView3] = useInView()
  const [ref4, inView4] = useInView()

  useEffect(() => {
      if (inView) {
        control.start("visible");
      } else {
        control.start("hidden");
      }
    }, [control, inView]);


  useEffect(() => {
      if (inView2) {
        control2.start("visible");
      } else {
        control2.start("hidden");
      }
    }, [control2, inView2]);

  useEffect(() => {
      if (inView3) {
        control3.start("visible");
      } else {
        control3.start("hidden");
      }
    }, [control3, inView3]);

  useEffect(() => {
      if (inView4) {
        control4.start("visible");
      } else {
        control4.start("hidden");
      }
    }, [control4, inView4]);

  const { loading, error, data } = useQuery(GET_CONTENT);
  if (loading) return <p></p>;
  if (error) return <p>Error :(</p>;
  return (
    <section className=" flex flex-col mx-auto relative lg:p-4 w-full xl:mt-5 mt-8 justify-between">
      <motion.div
         variants={heading_variants}
         ref = {ref3}
         initial = "hidden"
         animate = {control3}
         className="w-full text-center lg:mb-10 mb-2">
         <h4 className="w-50 font-bold text-4xl font-extrabold text-transparent text-8xl bg-clip-text bg-gradient-to-br from-steel-blue to-voilet">WHAT DO WE DO?</h4>
         <div className="bg-gradient-to-br from-steel-blue to-voilet h-[4px] w-[270px] mx-auto rounded-full"></div>
      </motion.div>
      <div className="flex flex-col xl:flex-row">
        <div className="flex flex-col w-auto xl:w-[33%] xl:text-left text-center justify-between  pt-10 xl:pb-20 pb-10">
          <motion.div
          variants={left_text_variants}
          ref = {ref3}
          initial = "hidden"
          animate = {control3}
          className="px-10 py-2 mx-auto w-[90%]">
            <h1 className="font-sans font-bold text-3xl mb-3">Customer Oriented</h1>
            <p className="text-mdlg">We put the needs of the customer over our business needs, in supporting our clients in business or project methods that involve; Design Thinking, Prototyping, and MVP. We are initially cost efficient and reduced.</p>
          </motion.div>
          <motion.div
          variants={left_text_variants}
          ref = {ref3}
          initial = "hidden"
          animate = {control3}
          className="px-10 py-2 mx-auto w-[90%]">
            <h1 className="font-sans font-bold text-3xl mb-3">Technical Innovations</h1>
            <p className="text-mdlg">We support in the early stage of each techinal innovation(design and implementation of websites, mobile app) and much at your service even with data science for entrepreneurial decision making, prediction of future events and machine learning.</p>
          </motion.div>
        </div>
        <motion.div
        variants={about_card_variants}
        ref = {ref}
        initial = "hidden"
        animate = {control}
        className="w-full xl:w-auto mx-auto text-center py-4 space-y-8 px-auto">
          {/*<img className="mx-auto w-[550px]" src={about_card}/>*/}
          <AboutCard/>
        </motion.div>
        <div className="flex flex-col w-auto xl:w-[33%] xl:text-right text-center justify-between pt-10 xl:pb-20 pb-10">
          <motion.div
          variants={right_text_variants}
          ref = {ref4}
          initial = "hidden"
          animate = {control4}
          className="px-10 py-2 mx-auto w-[90%]">
            <h1 className="font-sans font-bold text-3xl mb-3">Startup Partnership</h1>
            <p className="text-mdlg">We would like to support you in the early start-up phase to help your company in the neccesary decisions for evolution, to cover all your techinal necessities like website creation and much more, for a long-term partnership.</p>
          </motion.div>
          <motion.div
          variants={right_text_variants}
          ref = {ref4}
          initial = "hidden"
          animate = {control4}
          className="px-10 py-2 mx-auto w-[90%]">
            <h1 className="font-sans font-bold text-3xl mb-3">Project Buildiing</h1>
            <p className="text-mdlg">For professional designs, for your applications, scope of work, collection of data and much more.</p>
          </motion.div>
        </div>
      </div>
      <motion.div
      variants={extra_variants}
      ref = {ref2}
      initial = "hidden"
      animate = {control2}
      className="flex flex-col xl:flex-row my-20 px-10">
        <div className="xl:w-[40%] w-auto">
          <img className="mx-auto w-[90%]" src={about_extra_1}/>
        </div>
        <div className="xl:w-[60%] w-auto">
          <img className="mx-auto w-[95%]" src={about_extra_2}/>
        </div>
      </motion.div>
    </section>
  );
}

export default About;
