import React from 'react';
import { Link } from "react-router-dom";
import { camsol_logo_shaded } from "../../resources/images";
import { german_flag } from "../../resources/images";
import { english_flag } from "../../resources/images";
import { french_flag } from "../../resources/images";
import { linkedin_logo } from "../../resources/images";
import { tiktok_logo } from "../../resources/images";

import styles from "./FooterBar.module.css";

const FooterBar = () => {
  return (
    <div className={`${styles.footerWrapper} bg-gradientBG bg-cover`}>
      <div className={`${styles.footerTopBox}`}>
        <div className={`${styles.footerLogoBox} xl:w-[33%] md:w-[90%] text-white`}>
          <a href="/" className="inline-flex mx-auto mb-3 flex-col text-center w-[60px] lg:w-[125px]">
            <img className="h-auto w-[110px] mx-auto" alt="camsol logo" src={camsol_logo_shaded} />
          </a>
          <span className="text-mdlg lg:text-xl text-center text-white font-light w-[80%] xl:w-[40%]">Dive Along With us into a Multiverse of Innovations!</span>
        </div>

        <div className={`${styles.footerLinksWrapper} mt-3 xl:w-[22%] w-[50%] xl:text-left text-center text-white`}>
          <p className={`hover:font-semibold`}>
            <span className="text-Tmd xl:text-2xl font-bold font-serif">Our Offers</span>
          </p>
          <Link to="/" className={`${styles.footerLinks} hover:underline hover:font-semibold`}>
          Incubation
          </Link>
          <Link to="/" className={`${styles.footerLinks} hover:underline hover:font-semibold`}>
          Scale
          </Link>
          <Link to="/" className={`${styles.footerLinks} hover:underline hover:font-semibold`}>
          Performance
          </Link>
        </div>

        <div className={`${styles.footerLinksWrapper} } xl:w-[22%] w-[50%] mt-3 xl:text-left text-center text-white`}>
          <p className={`hover:font-semibold`}>
            <span className="text-Tmd xl:text-2xl font-bold font-serif">About Us</span>
          </p>
          <Link to="/" className={`${styles.footerLinks} hover:underline hover:font-semibold`}>
          Privacy Policy
          </Link>
          <Link to="/" className={`${styles.footerLinks} hover:underline hover:font-semibold`}>
          Carrers
          </Link>
          <a href="mailto:info@camsol.io" className={`${styles.footerLinks} hover:underline hover:font-semibold`}>
          info@camsol.io
          </a>
        </div>

        <div className={`${styles.footerLinksWrapper} xl:w-[22%] mx-auto mt-3 xl:text-left md:text-left text-white`}>
          <p className={`hover:font-semibold`}>
            <span className="text-Tmd xl:text-2xl font-bold font-serif">Social</span>
          </p>
          <Link to="/" className={`${styles.footerLinks} hover:underline hover:font-semibold`}>
            <div className="flex flex-row"><span className="my-auto w-[75px]">LinkedIn</span><img className="h-[30px] w-[30px] mx-3 rounded-md" alt="german_flag" src={linkedin_logo} /></div>
          </Link>
          <Link to="/" className={`${styles.footerLinks} hover:underline hover:font-semibold`}>
            <div className="flex flex-row"><span className="my-auto w-[70px]">TikTok</span><img className="h-[40px] w-[40px] mx-3 rounded-md" alt="german_flag" src={tiktok_logo} /></div>
          </Link>
        </div>
      </div>
      <hr className="mb-3 mx-20"></hr>
      <div className={`${styles.footerBottomBox}`}>
        <div className={`${styles.footerFlagImages} xl:w-[70%] `}>
          <div>
            <a href="/" className="inline-flex mx-2 flex-col text-center w-[50px]">
              <img className="h-[50px] w-[50px] rounded-full" alt="german_flag" src={german_flag} />
            </a>
            <a href="/" className="inline-flex mx-2 flex-col text-center w-[50px]">
              <img className="h-[50px] w-[50px] rounded-full" alt="french_flag" src={french_flag} />
            </a>
            <a href="/" className="inline-flex mx-2 flex-col text-center w-[50px]">
              <img className="h-[50px] w-[50px] rounded-full" alt="english_flag" src={english_flag} />
            </a>
          </div>
          <div className="auto"></div>
        </div>
        <div className={`${styles.footerBottomLinks} w-[50%] xl:text-right text-center`}>
          <Link to="/" className={`${styles.footerLinks} tracking-widest text-white font-bold hover:underline hover:font-semibold`}>
            &copy; 2022 COPYRIGHT
          </Link>
        </div>
      </div>
    </div>
  );
};

export default FooterBar;
