import React from 'react'

function ContactForm() {
    return (
      <div className="p-2">
        <h3 className="font-medium text-Tlg font-sans">Get In Touch</h3>
        <form>
          <label htmlFor="name" className="block my-6">
            <input
              name="name"
              type="text"
              placeholder="Enter Your Name"
              className="focus:bg-almost-white rounded-md bg-form-bg mt-1 block w-full px-3 py-4 text-Tsm placeholder-form-txt focus:outline-none focus:ring focus:ring-violet-300"
            />
          </label>
          <label htmlFor="email" className="block my-6">
            <input
              name="email"
              type="email"
              placeholder="Enter Your Email"
              className="focus:bg-almost-white rounded-md bg-form-bg mt-1 block w-full px-3 py-4 text-Tsm placeholder-form-txt focus:outline-none focus:ring focus:ring-violet-300"
            />
          </label>
          <label htmlFor="message" className="block my-6">
            <textarea
              name="message"
              type="text"
              placeholder="Enter Your Message"
              maxLength="500"
              rows="6"
              className="focus:bg-almost-white rounded-md bg-form-bg mt-1 block w-full px-3 py-4 text-Tsm placeholder-form-txt focus:outline-none focus:ring focus:ring-violet-300"
            />
          </label>
        </form>
        <button name="submit" type="submit" className="w-full font-semibold bg-gradient-to-r from-steel-blue to-violet text-almost-white text-Tmd py-3 px-4 mt-10 mb-5 rounded-xl ">
          Submit
        </button>
      </div>
    );
}

export default ContactForm
