import React  from "react";
import "./DiamondImage.css"
import { motion, useAnimation } from "framer-motion";
import { useEffect } from "react";
import { useInView } from "react-intersection-observer";


const slide_left_variants = {
  visible: { opacity: 1, x: 0, transition: { duration: .6 }},
  hidden: { opacity: 0, x: -100, transition: { duration: .6 }},
}

const DiamondImage = ({ image_name, person_name, person_position}) => {
  const control = useAnimation()
  const [ref, inView] = useInView()
  useEffect(() => {
      if (inView) {
        control.start("visible");
      } else {
        control.start("hidden");
      }
    }, [control, inView]);
  return (
    <motion.div
       variants={slide_left_variants}
       ref = {ref}
       initial = "hidden"
       animate = {control}
       className="mx-8">
      <div class="picture_big">
        <div class="picture">
          <img src={image_name} alt="" />
        </div>
      </div>
      <div className="items-center mx-auto text-center">
        <h4 className="text-white font-bold font-mono text-xl mt-1 drop-shadow-lg shadow-black">{person_name}</h4>
        <h5 className="text-white font-semibold font-display text-xl mb-1 drop-shadow-lg shadow-black">{person_position}</h5>
      </div>
    </motion.div>
  );
};

export default DiamondImage;
