import React from "react";
import { motion, useAnimation } from "framer-motion";
import { useEffect } from "react";
import { useInView } from "react-intersection-observer";
import { CardContent, ServiceCard } from "../../components";
import { illustration, person_camsol} from "../../resources/images";
import { useQuery } from "@apollo/client";
import { GET_CONTENT } from "../../data";

const heading_variants = {
  visible: { opacity: 1, y: 0, transition: { duration: .6 }},
  hidden: { opacity: 0, y: -100, transition: { duration: .6 }},
}
const card_variants = {
  visible: { opacity: 1, x: 0, transition: { duration: .6 }},
  hidden: { opacity: 0, x: -50, transition: { duration: .6 }},
}

function Services() {
  const { loading, error, data } = useQuery(GET_CONTENT);

  if (loading) return <p></p>;
  if (error) return <p>Error :(</p>;

  const control = useAnimation()
  const control1 = useAnimation()
  const control2 = useAnimation()
  const control3 = useAnimation()

  const [ref, inView] = useInView()
  const [ref1, inView1] = useInView()
  const [ref2, inView2] = useInView()
  const [ref3, inView3] = useInView()
  const ref_list = [ref1, ref2, ref3]


  useEffect(() => {
      if (inView) {
        control.start("visible");
      } else {
        control.start("hidden");
      }
    }, [control, inView]);

  useEffect(() => {
      if (inView1) {
        control1.start("visible");
      } else {
        control1.start("hidden");
      }
    }, [control1, inView1]);

  useEffect(() => {
      if (inView2) {
        control2.start("visible");
      } else {
        control2.start("hidden");
      }
    }, [control2, inView2]);

  useEffect(() => {
      if (inView3) {
        control3.start("visible");
      } else {
        control3.start("hidden");
      }
    }, [control3, inView3]);

  const control_list = [control1, control2, control3]

  return (

    <section id="services" className="relative h-auto pt-10 mb-20 items-center bg-cover m-0">
      <motion.div
         variants={heading_variants}
         ref = {ref}
         initial = "hidden"
         animate = {control}
         className="w-full text-center mb-2">
         <h4 className="w-50 font-bold text-4xl font-extrabold text-transparent text-8xl bg-clip-text bg-gradient-to-br from-steel-blue to-voilet">OUR SERVICES</h4>
         <div className="bg-gradient-to-br from-steel-blue to-voilet h-[4px] w-[270px] mx-auto rounded-full"></div>
         <p className="font-sans xl:w-[50%] w-[90%] mx-auto mt-5 font-semibold xl:text-Tmd text-sm">Camsol of great innovations has alot to offer to her clients, Tech-wise, no matter the stage of your project or entreprise.</p>
      </motion.div>

      <div className="flex flex-row">
        <div className="flex flex-col flex-wrap w-full mx-5 w-[100%] xl:w-[70%] xl:space-x-2 justify-center space-y-4 items-center xl:flex-row">
          { data.servicesSection.map((card) => {
            let index = parseInt(card.id) - 1
            console.log(index)
            const ref_index = ref_list[index]
            const control_index = control_list[index]

            return (
              <motion.div
                variants={card_variants}
                ref = {ref_index}
                initial = "hidden"
                animate = {control_index}
                className="mt-5 xl:w-[45%] w-[96%] mx-auto" key={card.id}>
                <ServiceCard
                  image_id = {card.id}
                  service_heading = {card.title}
                  service_info = {card.footerContent}
                />
              </motion.div>
            )
          })
          }
        </div>
        <div className="xl:w-[20%] lg:w-[25%] w-[0%] xl:h-[680px] xl:mt-auto my-auto xl:ml-8 mr-5">
          <img src={person_camsol}/>
        </div>
      </div>
    </section>
  );
}

export default Services;
