import React  from "react";
import "./ServiceCard.css"
import { motion, useAnimation } from "framer-motion";
import { useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import {service_1, service_2, service_3 } from "../../resources/images";
import * as ReactDOM from 'react-dom/client';

function removeHTML(str){
    var tmp = document.createElement("DIV");
    tmp.innerHTML = str;
    return tmp.textContent || tmp.innerText || "";
}

const ServiceCard = ({ image_id, service_heading, service_info}) => {
  const [isBooped, setIsBooped] = useState(false);
  const [div, setDiv] = useState("");
  const timing = 300

  // formating function variables
  service_info = removeHTML(service_info)
  service_info = service_info.trim()
  const images = [service_1, service_2, service_3]
  const image_index = parseInt(image_id) - 1
  const image_name = images[image_index]

  // creating componenet dynamic styles
  const image_style = {
    marginRight: isBooped
    ? `20px`
    :'auto',
    marginLeft: isBooped
    ? `10px`
    :'auto',
    marginBottom: isBooped
    ? `0px`
    :'10px',
    width: isBooped
    ? `20%`
    :'35%',
    transform: isBooped
      ? `scale(.7)`
      : `scale(.85)`,
    transition: `all ${timing}ms`,
  };

  const classic_style = {
    display: 'flex',
    alignItems: 'center',
    flexDirection: isBooped
    ? `row`
    : `column`,
    height: isBooped
    ? `25%`
    :'100%',
    transition: `all ${timing}ms`,
  }

  const heading_style = {
    transform: isBooped
      ? `scale(.8)`
      : `scale(1)`,
    marginRight: 'auto',
    marginLeft: isBooped
    ? `0px`
    :'auto',
    width: isBooped
    ? `55%`
    :'100%',
    transition: `all ${timing}ms`,
  }

  const paragraph_style = {
    opacity: isBooped
    ? '1'
    :'0',
    paddingTop: isBooped
    ? '25px'
    : '0px',
    paddingBottom: isBooped
    ? '25px'
    : '0px',
    height: isBooped
    ? '75%'
    :'0px',
    transitionDelay: `100ms`,
    transition: `all ${timing}ms`,
  }

  // triggering hover events effects
  const trigger = () => {
    console.log("hovered")
    setIsBooped(true);
    setDiv(service_info)
  };
  const clear_trigger = () => {
    console.log("clear hovered")
    setIsBooped(false);
    setDiv("")
  }

  return (
    <div
    onMouseEnter = {trigger}
    onMouseLeave = {clear_trigger}
    id = "root"
    className="shadow-2xl w-full  mx-auto lg:mx-4 mx-auto sm:mt-20 lg:mt-10 hover:cursor-pointer h-[300px] py-5 justify-items-center space-x-0 text-center rounded-3xl flex flex-col justify-center">
      <div style={classic_style}>
        <img style = {image_style}  src={image_name}/>
        <h1 style = {heading_style} className="text-3xl sm:my-15 font-bold font-sans uppercase">{service_heading}</h1>
      </div>
      <div style={paragraph_style}><p className="xl:text-Tsm text-sm my-auto px-8 font-mono text-left">{div}</p></div>
    </div>
  );
};

export default ServiceCard;
