import React from "react";
import "./Contact.module.css";
import { LinkednIcon, ContactRight,TelephoneIcon,  HubspotForm, ContactForm } from "../../components";
import { useQuery } from "@apollo/client";
import { GET_CONTENT } from "../../data";
import { EmailIcon, GlobeIcon } from "../../components";

function Contact() {
  const { loading, error, data } = useQuery(GET_CONTENT);
  if (loading) return <p></p>;
  if (error) return <p>Error :(</p>;
  return (
    <section className="bg-contactBG relative h-auto items-center py-20 bg-center bg-cover">
      <div className="bg-almost-white h-auto w-card-90 static lg:max-w-[1024px] rounded-lg m-auto pt-4 mt-10 shadow-cardBox">
        <div className="px-2 lg:px-8 flex flex-col lg:flex-row w-full lg:space-x-8">
          <div className="lg:w-[70%] w-[100%]">
            <ContactForm/>
          </div>
          <div className="lg:top-[19%] lg:right-[180px]" style={{position: "absolute"}}>
            <ContactRight/>
          </div>
        </div>

        <div className="px-2 lg:px-8 lg:space-x-8 text-Tsm text-center xl:justify-left justify-center my-4 flex flex-row flex-wrap">
          <div className="my-2">
            <a
              href="mailto:info@camsol.io"
              className="hover:underline inline-flex items-center space-x-4"
              target="_blank"
              rel="noreferrer"
            >
              <EmailIcon />
              <span>info@camsol.io</span>
            </a>
          </div>
          <div className="my-2 pl-3">
            <a
              href="https://camsol.io"
              className="hover:underline inline-flex items-center space-x-4"
              target="_blank"
              rel="noreferrer"
            >
              <GlobeIcon />
              <span>https://camsol.io</span>
            </a>
          </div>
          <div className="my-2 pl-3">
            <a
              href="https://camsol.io"
              className="hover:underline inline-flex items-center space-x-4"
              target="_blank"
              rel="noreferrer">
              <TelephoneIcon />
              <span>+49 *** *** ***</span>
            </a>
          </div>
        </div>
        <div className="text-sm relative px-10 pb-8 w-full">
          <div
            dangerouslySetInnerHTML={{
              __html: data.contactSection[0].smallText,
            }}
          ></div>
        </div>
      </div>
    </section>
  );
}

export default Contact;
